import React,{useState} from 'react'
import useRegister from './useRegister'
import Spinner from '../Spinner'
import validate from './validate'
import BigButton from '../commonComponents/BigButton'

export default function FormSignup({
    submitForm,
    isLoading,
    onProvince,
    selected
    }) {

        const {handleChange,handleSubmit,values,errors} = useRegister(submitForm,validate)


    return (
        <form className="register_form" onSubmit={handleSubmit}>
            <div className="register_heading">
                        <h3 className="register_header">
                            To ti khon Cleverfood
                        </h3>
                        <p className="register_intro my-40">
                            Tip tc ti CleverFood
                        </p>
            </div>
            <div className="register_name my-20">
                        <div className="register_name-label-wrapper">
                            <label htmlFor="" className="register_name-label">
                                H v tn
                            </label>
                        </div>
                        <input  type="text" name="fullname" 
                        className={`register_name-input ${ errors.fullname ? "error-border" : ""}`}
                        placeholder="Nhp h v tn"
                                value={values.fullname}
                                onChange={handleChange}
                        />
                        {
                            errors.fullname &&
                            <p className="error">{errors.fullname}</p>
                        }
            </div>
            <div className="register_user my-20">
                        <div className="register_user-label-wrapper">
                            <label htmlFor="" className="register_user-label">
                                Tn ng nhp
                            </label>
                        </div>
                        <input  type="text" name="user"  className={`register_user-input ${ errors.user ? "error-border" : ""}`} placeholder="Nhp tn ng nhp"
                                 value={values.user}
                                 onChange={handleChange}
                        />
                        {
                            errors.user &&
                            <p className="error">{errors.user}</p>
                        }
            </div>
            <div className="register_mail my-20">
                        <div className="register_mail-label-wrapper">
                            <label htmlFor="" className="register_mail-label">
                                Email
                            </label>
                        </div>
                        <input  type="text" name="email"  className={`register_mail-input ${ errors.email ? "error-border" : ""}`} placeholder="Nhp email"
                                 value={values.email}
                                 onChange={handleChange}
                        />
                          {
                            errors.email &&
                            <p className="error">{errors.email}</p>
                        }
            </div>
            <div className="register-province my-20">
                <div className="register_province-label-wrapper">
                    <label htmlFor="" className="register_province-label">
                        Ni sinh sng
                    </label>
                </div>
                <select 
                onChange={onProvince}
                value={selected}
                className="register_province-selection mt-10">
                    <option value="An Giang">An Giang </option>
                    <option value="B Ra - Vng Tu ">B Ra - Vng Tu</option>
                    <option value="Bc Giang">Bc Giang </option>
                    <option value="Bc Kn">Bc Kn </option>
                    <option value="Bc Liu">Bc Liu </option>
                    <option value="Bc Ninh">Bc Ninh </option>
                    <option value="Bn Tre">Bn Tre </option>
                    <option value="Bnh nh">Bnh nh </option>
                    <option value="Bnh Dng">Bnh Dng</option> 
                    <option value="Bnh Phc">Bnh Phc </option>
                    <option value="Bnh Thun">Bnh Thun </option>
                    <option value="Bnh Thun">Bnh Thun </option>
                    <option value="C Mau">C Mau </option>
                    <option value="Cao Bng">Cao Bng </option>
                    <option value="k Lk">k Lk </option>
                    <option value="k Nng">k Nng </option>
                    <option value="in Bin">in Bin </option>
                    <option value="ng Nai">ng Nai </option>
                    <option value="ng Thp">ng Thp </option>
                    <option value="ng Thp">ng Thp </option>
                    <option value="Gia Lai">Gia Lai </option>
                    <option value="H Giang">H Giang </option>
                    <option value="H Nam">H Nam </option>
                    <option value="H Tnh">H Tnh </option>
                    <option value="Hi Dng">Hi Dng </option>
                    <option value="Hu Giang">Hu Giang </option>
                    <option value="Ha Bnh">Ha Bnh </option>
                    <option value="Hng Yn">Hng Yn </option>
                    <option value="Khnh Ha">Khnh Ha </option>
                    <option value="Kin Giang">Kin Giang </option>
                    <option value="Kon Tum">Kon Tum </option>
                    <option value="Lai Chu">Lai Chu </option>
                    <option value="Lm ng">Lm ng </option>
                    <option value="Lng Sn">Lng Sn </option>
                    <option value="Lo Cai">Lo Cai </option>
                    <option value="Long An">Long An </option>
                    <option value="Nam nh">Nam nh </option>
                    <option value="Ngh An">Ngh An </option>
                    <option value="Ninh Bnh">Ninh Bnh </option>
                    <option value="Ninh Thun">Ninh Thun </option>
                    <option value="Ph Th">Ph Th </option>
                    <option value="Qung Bnh">Qung Bnh </option>
                    <option value="Qung Bnh">Qung Bnh </option>
                    <option value="Qung Ngi">Qung Ngi </option>
                    <option value="Qung Ninh">Qung Ninh </option>
                    <option value="Qung Tr">Qung Tr </option>
                    <option value="Sc Trng">Sc Trng </option>
                    <option value="Sn La">Sn La </option>
                    <option value="Ty Ninh">Ty Ninh </option>
                    <option value="Thi Bnh">Thi Bnh </option>
                    <option value="Thi Nguyn">Thi Nguyn </option>
                    <option value="Thanh Ha">Thanh Ha </option>
                    <option value="Tha Thin Hu">Tha  Thin Hu</option>
                    <option value="Tin Giang">Tin Giang </option>
                    <option value="Tr Vinh">Tr Vinh </option>
                    <option value="Tuyn Quang">Tuyn Quang </option>
                    <option value="Vnh Long">Vnh Long </option>
                    <option value="Vnh Phc">Vnh Phc </option>
                    <option value="Yn Bi">Yn Bi </option>
                    <option value="Ph Yn">Ph Yn </option>
                    <option value="Tp.Cn Th">Tp .Cn Th</option>
                    <option value="Tp. Nng">Tp . Nng</option>
                    <option value="Tp.Hi Phng">Tp .Hi Phng</option>
                    <option value="Tp.H Ni">Tp .H Ni</option>
                    <option value="TP  HCM">TP HCM </option>
                </select>
            </div>
            <div className="register_address-my-20">
                <div className="register_address-label-wrapper">
                    <label htmlFor="" className="register_address-label">
                        a ch
                    </label>
                </div>
                <input  
                type="text" 
                name="address"  
                className=
                {`register_mail-input ${ errors.address ? "error-border" : ""}`} placeholder="Nhp a ch"
                value={values.address}
                onChange={handleChange}
                />
                    {
                    errors.address &&
                    <p className="error">{errors.address}</p>
                }
            </div>
            <div className="register_password my-20">
                        <div className="register_password-label-wrapper">
                            <label htmlFor="" className="register_password-label">
                                Mt khu
                            </label>
                        </div>
                        <input  type="password" name="password"  className={`register_password-input ${ errors.password ? "error-border" : ""}`} placeholder="Nhp mt khu"
                                 value={values.password}
                                 onChange={handleChange}
                        />
                          {
                            errors.password &&
                            <p className="error">{errors.password}</p>
                        }
            </div>
            <div className="register_password-confirm my-20">
                        <div className="register_password-confirm-label-wrapper">
                            <label htmlFor="" className="register_password-confirm-label">
                                Xc nhn mt khu
                            </label>
                        </div>
                        <input  type="password" name="confirmPassword"  className={`register_password-input ${ errors.confirmPassword ? "error-border" : ""}`} placeholder="Xc nhn mt khu"
                                 value={values.confirmPassword}
                                 onChange={handleChange}
                        />
                          {
                            errors.confirmPassword &&
                            <p className="error">{errors.confirmPassword}</p>
                        }
            </div>
            <div className="register_button my-20">
                        <BigButton
                        type="submit"
                        text="NG K"
                        btnStyle="primary-green"
                        />
            </div>
            {
                isLoading && 
                <Spinner style={{fontSize: "3rem"}}/>
            }
        </form>   
            
             

    )
}
