
export default function validate(values,accounts) {
    let errors = {}
    const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    if(!values.fullname.trim()) {
        errors.fullname = "Vui lng nhp h tn"
    }

    if(!values.user.trim()) {
        errors.user = "Vui lng nhp tn ti khon"
    } else if(accounts.some((item) => item.account_user === values.user)) {
        errors.user = "Ti khon  tn ti"
    }



   

    if(!values.email.trim()) {
        errors.email = "Vui lng nhp a ch email"
    } else if(!re.test(values.email)) {
        errors.email = "Vui lng nhp email hp l" 
    }

    if(!values.address) {
        errors.address = "Vui lng nhp a ch"
    }

    if(!values.password) {
        errors.password = "Vui lng nhp mt khu"
    } else if(values.password.length < 6) {
        errors.password = "Vui lng nhp ti thiu mt khu trn 6 k t"
    }

    if(!values.confirmPassword) {
        errors.confirmPassword = "Vui lng nhp li mt khu"
    } else if(values.confirmPassword !== values.password) {
        errors.confirmPassword = "Mt khu khng trng nhau"
    }


    return errors;
}